/* 重置 */
body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,form,fieldset,input,textarea,p,blockquote,th,td{margin:0;padding:0;}
table{border-collapse:collapse;border-spacing:0;}
fieldset,img{border:0;}
address,caption,cite,code,dfn,em,strong,th,var{font-style:normal;font-weight:normal;}
ol,ul{list-style:none;}
caption,th{text-align:left;}
h1,h2,h3,h4,h5,h6{font-size:100%;font-weight:normal;}
q:before,q:after{content:"";}
abbr,acronym{border:0;}
body{color:#666;background-color:#fff;font:12px/1.5 '微软雅黑',tahoma,arial,'Hiragino Sans GB',宋体,sans-serif;}
.clearfix:after{visibility:hidden;display:block;font-size:0;content:"";height:0;clear:both;}
.clearfix{zoom:1;}
.text-ellipsis{white-space:nowrap;/*设置不折行*/text-overflow:ellipsis;/*这就是省略号喽*/-o-text-overflow:ellipsis;overflow:hidden;/*设置超过的隐藏*/}
/*lightbox图片画廊样式*/
#G-lightbox-mask{
	position:fixed;
	left:0;
	top:0;
	width:100%;
	height:100%;
	background-color:rgba(0,0,0,0.5);
	z-index:10000;
	display:none;
	background:url(../images/opacity.png) repeat 0 0;
}

#G-lightbox-popup{
	display:none;
	position:fixed;
	left:50%;
	top:0;
	margin-left:-150px;
	width:300px;
	height:300px;
	z-index:10001;
	.lightbox-pic-view{
		position:relative;
		border:5px solid #fff;
		border-radius:3px;
		background:#fff url(../images/loading.gif) no-repeat center center;
		overflow:hidden;
		.lightbox-btn{
			position:absolute;
			top:0;
			width:40%;
			height:100%;
			z-index:1;
			background:url(../images/transparent.png) repeat 0 0;
			&.lightbox-prev-btn{
				left:0;
			}
			&.lightbox-prev-btn-show{
				cursor:pointer;
				background:url(../images/prev.png) no-repeat left center;
			}
			&.lightbox-next-btn{
				right:0;
			}
			&.lightbox-next-btn-show{
				cursor:pointer;
				background:url(../images/next.png) no-repeat right center;
			}
		}
		.lightbox-image{
			display:block;
		}
	}

	.lightbox-pic-caption{
		position:relative;
		z-index:2;
		margin:0 5px;
		margin-top:-60px;
		background-color:rgba(0,0,0,0.5);
		background:url(../images/opacity.png) repeat 0 0;
		.lightbox-caption-area{
			padding:10px 5px;
			color:#fff;
			.lightbox-of-index{
				color:#999;
			}
		}
		.lightbox-close-btn{
			position:absolute;
			top:13px;
			right:10px;
			width:27px;
			height:27px;
			background:url(../images/close.png) no-repeat center center;
		}
	}
}
*html #G-lightbox-mask{position:absolute;}
*html #G-lightbox-popup{position:absolute;}